.me {
    margin-top: 10rem;
    font-size: 2rem;
    text-align: center;
    color:var(--color-light);
}

.scroll__down {
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    position: absolute;
    right: -10%;
    bottom: 3.5rem;
}

/* Responsive Design */
/* Media Queries (Medium Devices) Tablets */
@media screen and (max-width: 1024px) {
    .me {
    margin-top: 5rem;
    font-size: 1.5rem;
    text-align: center;
    color:var(--color-light);
}

    header {
        height: 80vh;
    }

    .cta {
    gap: 1rem;
    margin-top: 0.8rem;
    font-size: 0.9rem;
    }

}

/* Media Queries (small Devices) phones */
@media screen and (max-width: 600px) {
    .me {
    margin-top: 10rem;
    font-size: 1.3rem;
    text-align: center;
    color:var(--color-light);
}
    header {
        height: 100vh;
    }

    .header__socials, .scroll__down {
        display:none;
    }
}