.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    /*height always matches the width*/
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary), transparent);
    display: grid;
    place-items: center;

}

.about__me-image {
    width: auto;
    height: 378px;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}


.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 0.3rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}


.about-text {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    margin: 2rem - 2.6rem;
    color:var(--color-light);

}

/* Responsive Design */

@media screen and (max-width: 3440px) {
   .about__me-image {
    width: 100%;
    height: 100%;
    }
}


/* Media Queries (Medium Devices) Tablets */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

   .about__me-image {
    width: auto;
    height: 100%;
    border-radius: 2rem;    
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    }

    .about__cards {
    grid-template: 1fr 1fr;
    gap: 1rem;
    }

    .about-text {
         margin: 1rem 0 1.5rem;
    }


}

/* Media Queries (small Devices) phones */
@media screen and (max-width: 600px) {
    
    .about__me-image {
    width: auto;
    height: 325px;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    }

.about__me {
    width:65;
    height:20rem;
    margin: 0 auto 3rem;
}
.about__cards {
    grid-template: 1fr 1fr;
    gap: 1rem;
}

.about-text {
    margin: 1.5rem 0;
}

}

@media screen and (max-width: 500px) {

    .about__me-image {
    width: 100%;
    height: 100%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    }

    
.about__me {
    width:50;
    height:15rem;
   font-size: 5rem;
}
}

@media screen and (max-width: 380px) {


    .about__me-image {
    width: 100%;
    height: 100%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    }

    
.about__me {
    position: relative;
    width:25;
    height:15rem;
    right:15px;
}


    .about__cards {
    grid-template: 1fr 1fr;
    gap: 0.3rem;
    position: relative;
    right:15px;
    font-size: 0.2rem;
    }

    .about__card {
        width: 115px;
        height: 170px;
        font-size: 0.5rem;
         padding: 0.5rem; 
    }

    .about-text {
        position: relative;
        right:5px;
        font-size: 1rem;
    }


}