@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

/* '*' all elements */
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

/* css variables prevents from repeating ourselves. */
:root {

    /*#1F1F38*/
    
    --color-bg: #000833;
    --color-bg-variant: #2C2C6C;
    --color-primary: #4DB5ff;
    --color-primary-variant: rgba(77,181,255,0.4);
    --color-white: #FFF;
    --color-light: rgba(255,255,255,0.6);


    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

   
    --transition: all 400ms ease;
}


html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    /* using CSS variables gives the background color of --color-bg*/
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg-texture.png);
}

/* General Styles */

.container {
    width: var(--container-width-lg);
    /* positions in the middle */
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

/* We have white space around different components.*/
section {
    margin-top: 8rem;
    height: 100vh;
}

/* Every section is going to have an h2 & h5 */
 section > h2,
 section > h5 {
     text-align: center;
     color:var(--color-light)
}

/* h2 is going to get the color-primary */
section > h2 {
    color:var(--color-primary);
    margin-bottom: 3rem;
}

/* text-light class will have a color-light */
.text-light {
    color:var(--color-light);
}


/* Default styles for Links */
a {
    color: var(--color-primary);
    transition: var(--transition);
}

/* Links, on hover turn white */
a:hover {
    color:var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    border-radius: 1rem;
}
/* button class on hover */
.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}




.btn-primary {
 background: var(--color-primary);
 color: var(--color-bg);
 border-radius: 1rem;
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* Media Queries (Medium Devices) Tablets */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* Media Queries (small Devices) phones */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}